ion: relative;\r\n    display: flex;\r\n    justify-content: center;\r\n    align-items: center;\r\n    gap: 5vw;\r\n    max-width: 1200px;\r\n    width: 100%;\r\n    z-index: 1;\r\n\r\n    @media (max-width: $tablet-breakpoint) {\r\n      flex-direction: column;\r\n      gap: 30px;\r\n    }\r\n  }\r\n\r\n  &__first-column {\r\n    width: 433px;\r\n    transform: translateY(-50px);\r\n\r\n    @media (max-width: $tablet-breakpoint) {\r\n      transform: none;\r\n      width: 100%;\r\n      max-width: 600px;\r\n      text-align: center;\r\n    }\r\n  }\r\n\r\n  &__title {\r\n    @include h1;\r\n    margin-bottom: 2vh;\r\n\r\n    @media (max-width: $tablet-breakpoint) {\r\n      font-size: 2.5rem;\r\n      margin-bottom: 15px;\r\n    }\r\n\r\n    @media (max-width: $mobile-breakpoint) {\r\n      font-size: 2rem;\r\n      line-height: 1.2;\r\n    }\r\n  }\r\n\r\n  &__text {\r\n    @include text;\r\n    margin-bottom: 3vh;\r\n\r\n    @media (max-width: $tablet-breakpoint) {\r\n      font-size: 1rem;\r\n      margin-bottom: 20px;\r\n    }\r\n\r\n    @media (max-width: $mobile-breakpoint) {\r\n      font-size: 0.9rem;\r\n    }\r\n  }\r\n\r\n  &__second-column {\r\n    display: grid;\r\n    grid-template-columns: repeat(2, 1fr);\r\n    gap: 15px;\r\n    width: 400px;\r\n    transform: translateY(-50px);\r\n    position: relative;\r\n    top: 50px;\r\n\r\n    @media (max-width: $tablet-breakpoint) {\r\n        display: none;\r\n      }\r\n\r\n      @media (max-width: $mobile-breakpoint) {\r\n        display: none;\r\n      }\r\n  }\r\n\r\n  &__item {\r\n    width: 180px;\r\n    height: 250px;\r\n    background-color: rgb(255, 255, 255);\r\n    display: flex;\r\n    flex-direction: column;\r\n    position: relative;\r\n    color: black;\r\n\r\n    &:nth-child(1) {\r\n        top: 0;\r\n      }\r\n      &:nth-child(2) {\r\n        top: 40px;\r\n      }\r\n      &:nth-child(3) {\r\n        top: 20px;\r\n      }\r\n      &:nth-child(4) {\r\n        top: 80px;\r\n      }\r\n\r\n      @media (max-width: $mobile-breakpoint) {\r\n        width: 100%;\r\n        height: 200px;\r\n    }\r\n  }\r\n\r\n  &__button {\r\n    @include buttonGreen;\r\n\r\n    @media (max-width: $mobile-breakpoint) {\r\n      padding: 12px 25px;\r\n      font-size: 0.9rem;\r\n    }\r\n  }\r\n\r\n  @media (max-width: $tablet-breakpoint) {\r\n    min-height: 600px;\r\n    padding: 40px 20px;\r\n    gap: 30px;\r\n  }\r\n\r\n  @media (max-width: $mobile-breakpoint) {\r\n    min-height: 500px;\r\n    padding: 20px 15px;\r\n    gap: 20px;\r\n  }\r\n}","@use \"sass:color\";\r\n$tablet-breakpoint: 768px;\r\n$mobile-breakpoint: 480px;\r\n\r\n@mixin h1 {\r\n    font-family: Clip;\r\n    font-weight: 400;\r\n    font-size: 45px;\r\n    line-height: 120%;\r\n    letter-spacing: 2%;\r\n    vertical-align: middle;\r\n}\r\n\r\n@mixin h2 {\r\n    font-family: Clip ;\r\n    font-weight: 400;\r\n    font-size: 32px;\r\n    line-height: 120%;\r\n    letter-spacing: 2%;\r\n    vertical-align: middle;\r\n\r\n\r\n}\r\n\r\n@mixin text{\r\n    font-family: Inter;\r\n    font-weight: 400;\r\n    font-size: 16px;\r\n    line-height: 150%;\r\n    letter-spacing: 0%;\r\n    vertical-align: middle;\r\n\r\n}\r\n\r\n$baseColor: #76B72A;\r\n\r\n@mixin fade-transition {\r\n    transition: all 0.3s ease-in-out;\r\n}\r\n\r\n@mixin buttonGreen{\r\n    width: 155px;\r\n    height: 52px;\r\n    gap: 8px;\r\n    border-radius: 100px;\r\n    padding-right: 32px;\r\n    padding-left: 32px;\r\n    background-color: $baseColor;\r\n    cursor: pointer;\r\n\r\n    &--active{\r\n        background: color.adjust($baseColor, $lightness: -10%);\r\n    }\r\n}\r\n\r\n@mixin buttonTransparent{\r\n    width: 207px;\r\n    height: 52px;\r\n    gap: 8px;\r\n    border-radius: 100px;\r\n    border-width: 1px;\r\n    border: 1px solid ;\r\n    cursor: pointer;\r\n    background: transparent;\r\n}\r\n\r\n@mixin logo($size: 100px, $hover-effect: true) {\r\n    width: $size;\r\n    height: $size;\r\n    cursor: pointer;\r\n    object-fit: contain;\r\n    \r\n    @if $hover-effect {\r\n      transition: transform 0.2s ease;\r\n      \r\n      &:hover {\r\n        transform: scale(1.05);\r\n      }\r\n    }\r\n    \r\n    @media (max-width: $tablet-breakpoint) {\r\n      width: $size * 0.8;\r\n      height: $size * 0.8;\r\n    }\r\n    \r\n    @media (max-width: $mobile-breakpoint) {\r\n      width: $size * 0.6;\r\n      height: $size * 0.6;\r\n    }\r\n  }","@use '../components/mixins' as *;\r\n\r\n.sectionContacts {\r\n    padding: 60px 0;\r\n    position: relative;\r\n\r\n    &__button{\r\n        @include buttonGreen;\r\n    }\r\n\r\n    &__h1 {\r\n        @include h1;\r\n        margin-bottom: 40px;\r\n        padding-left: 15vw;\r\n    }\r\n\r\n    &__container {\r\n        display: flex;\r\n        gap: 30px;\r\n        max-width: 1200px;\r\n        margin: 0 auto;\r\n        padding: 0 15px;\r\n\r\n        &__firstColumn, &__secondColumn {\r\n            padding: 40px;\r\n            border: 1px solid #ddd;\r\n            border-radius: 35px;\r\n            background: white;\r\n            box-shadow: 0 4px 6px rgba(0,0,0,0.1);\r\n        }\r\n\r\n        &__firstColumn {\r\n            flex: 1;\r\n            display: flex;\r\n            flex-direction: column;\r\n            gap: 30px;\r\n\r\n            .contacts-list {\r\n                display: flex;\r\n                flex-direction: column;\r\n                gap: 25px;\r\n\r\n                .contact-item {\r\n                    display: flex;\r\n                    flex-direction: column;\r\n                    gap: 10px;\r\n\r\n                    .contact-label {\r\n                        @include text;\r\n                        color: #333;\r\n                    }\r\n\r\n                    .contact-text {\r\n                        @include text;\r\n                        color: #666;\r\n                        text-decoration: none;\r\n\r\n                        &:hover {\r\n                            color: #70CAF3;\r\n                        }\r\n                    }\r\n                }\r\n            }\r\n        }\r\n\r\n        &__secondColumn {\r\n            flex: 2;\r\n            background: rgb(112 176 243 / 30% );\r\n\r\n            .contact-form {\r\n                display: flex;\r\n                flex-direction: column;\r\n                gap: 25px;\r\n\r\n                .form-row {\r\n                    display: flex;\r\n                    gap: 20px;\r\n\r\n                    .input-group {\r\n                        flex: 1;\r\n                    }\r\n                }\r\n\r\n                .input-group {\r\n                    display: flex;\r\n                    flex-direction: column;\r\n                    gap: 8px;\r\n\r\n                    label {\r\n                        @include text;\r\n                        color: #333;\r\n                    }\r\n\r\n                    .form-input {\r\n                        @include text;\r\n                        padding: 12px 15px;\r\n                        border: 1px solid #ddd;\r\n                        border-radius: 8px;\r\n                        transition: border-color 0.3s;\r\n\r\n                        &:focus {\r\n                            border-color: #70CAF3;\r\n                            outline: none;\r\n                        }\r\n\r\n                        &.textarea {\r\n                            resize: vertical;\r\n                            min-height: 100px;\r\n                        }\r\n                    }\r\n                }\r\n\r\n                .form-notice {\r\n                    @include text;\r\n                    font-size: 0.9em;\r\n                    color: #666;\r\n                    text-align: center;\r\n                    margin-top: 15px;\r\n                    opacity: 80%;\r\n                }\r\n                \r\n                .form-submit-wrapper {\r\n                    display: flex;\r\n                    align-items: center;\r\n                    gap: 20px;\r\n                    margin-top: 20px;\r\n                }\r\n            \r\n                .success-message {\r\n                    @include text;\r\n                    color: #28a745;\r\n                    padding: 10px 15px;\r\n                    background: #d4edda;\r\n                    border-radius: 5px;\r\n                    border: 1px solid #c3e6cb;\r\n                    display: none; // Скрыто по умолчанию\r\n                    animation: fadeIn 0.3s ease;\r\n                    @include fade-transition;\r\n                    opacity: 0;\r\n                    visibility: hidden;\r\n            \r\n                    &.visible {\r\n                        opacity: 1;\r\n                        visibility: visible;\r\n                    }\r\n\r\n                    &::before {\r\n                        content: \"✓\";\r\n                        margin-right: 8px;\r\n                    }\r\n                }\r\n            \r\n                @keyframes fadeIn {\r\n                    from { opacity: 0; transform: translateX(10px); }\r\n                    to { opacity: 1; transform: translateX(0); }\r\n                }\r\n            }\r\n        }\r\n    }\r\n}\r\n\r\n@media (max-width: 768px) {\r\n    .sectionContacts__container {\r\n        flex-direction: column;\r\n        \r\n        &__firstColumn, &__secondColumn {\r\n            width: 100% !important;\r\n        }\r\n        \r\n        .form-row {\r\n            flex-direction: column;\r\n        }\r\n    }\r\n}","@use '../components/mixins' as *;\r\n\r\n.sectionGeografy {\r\n    height: 550px;\r\n    border-radius: 60px;\r\n    background: #3262AC;\r\n    color: white;\r\n    margin: 2%;\r\n    width: 96%;\r\n    display: flex;\r\n    columns: 2;\r\n    align-items: center;\r\n    justify-content: center;\r\n    position: relative;\r\n    overflow: hidden;\r\n    gap: 2vw;\r\n\r\n    &__container {\r\n        width: 45%;\r\n        he